import React, { useState } from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { FluentProvider } from '@fluentui/react-components';
import { planktonTheme } from '../planktonTheme';
import * as styles from "./contact.module.css";
import { Mail48Regular, Call48Regular } from '@fluentui/react-icons';
import {
    Input,
    Textarea,
    Button
} from '@fluentui/react-components';
import { graphql } from 'gatsby';
import {useTranslation} from 'gatsby-plugin-react-i18next';

const ContactPage: React.FC = () => {
    const initialFormState = {
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        message: ''
      };

      const [formData, setFormData] = useState(initialFormState);

      const { t, i18n } = useTranslation();

      const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
      }; 
      const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        console.log(formData);
        fetch('https://your-api-id.execute-api.region.amazonaws.com/prod/contact', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(formData),
        })
          .then((response) => response.json())
          .then((data) => {
            console.log('Success:', data);
            setFormData(initialFormState); 
          })
          .catch((error) => {
            console.error('Error:', error);
          });
      };
      
      return (
    <Layout>
        <FluentProvider theme={planktonTheme} >
            <div className="flex-row ">
           <div className="flex-row flex-8" style={{marginTop: '150px'}}>
            <div className="flex-column flex-6">
            <form className="flex-column" onSubmit={handleSubmit}>    
                <div className="flex-column flex-10" style={{paddingBottom: '10px'}}>
                    <label className="flex-8" htmlFor="firstName">{t('First Name')}:</label>
                    <Input
                        value={formData.firstName}
                        onChange={handleChange}
                        id="firstName"
                        name="firstName"
                        required
                        className="flex-8"
                    />
                </div>
                <div className="flex-column flex-10" style={{paddingBottom: '10px'}}>
                <label className="flex-8" htmlFor="lastName">{t('Last Name')}:</label>
                    <Input
                        value={formData.lastName}
                        onChange={handleChange}
                        id="lastName"
                        name="lastName"
                        required
                        className="flex-8"
                    />
                </div>
                <div className="flex-column flex-10" style={{paddingBottom: '10px'}}>
                <label className="flex-8" htmlFor="email">{t('Email')}:</label>
                    <Input
                        value={formData.email}
                        onChange={handleChange}
                        id="email"
                        name="email"
                        required
                        className="flex-8"
                    />
                </div>
                <div className="flex-column flex-10" style={{paddingBottom: '10px'}}>
                    <label className="flex-8" htmlFor="phone">{t('Phone')}:</label>
                    <Input
                        value={formData.phone}
                        onChange={handleChange}
                        id="phone"
                        name="phone"
                        required
                        className="flex-8"
                    />
                </div>
                <div className="flex-column flex-10" style={{paddingBottom: '10px'}}>
                    <label className="flex-8" htmlFor="message">{t('Message')}:</label>
                    <Textarea
                        value={formData.message}
                        onChange={handleChange}
                        id="message"
                        name="message"
                        rows={5} // Set the number of rows
                        required
                        className="flex-8"
                    />
                </div>
                <div className="flex-row flex-10">
                <Button className={`${styles.button} flex-8`} type="submit">{t('Submit')}</Button>
                </div>
                </form>
            </div>
            <div className="flex-column flex-6">
                <div className="flex-column" style={{padding: "20px"}}>                
                    <div className="flex-row" style={{fontSize: '55px', fontWeight:'400', lineHeight: 1.2, paddingBottom:'50px'}}>{t('Get in touch with us today')}</div>
                    <div className="flex-row flext-6" style={{paddingBottom:'20px', justifyContent:'flex-start', width:'100%'}}><Call48Regular style={{paddingRight: '10px'}}/><div style={{fontSize: '16px', fontWeight:'400', lineHeight: 1.5}}><div>{t('Phone')}:</div> <div style={{fontWeight:'bold'}}>+41 76 814 25 00</div></div></div>
                    <div className="flex-row flext-6" style={{justifyContent:'flex-start', width:'100%'}}><Mail48Regular style={{paddingRight: '10px'}}/> <div style={{fontSize: '16px', fontWeight:'400', lineHeight: 1.5}}><div>{t('Email')}:</div> <div style={{fontWeight:'bold'}}>info@planktonsolutions.com</div></div></div>
                </div>
            </div>
           </div>
           </div>
        </FluentProvider>
    </Layout>
)
}
export const Head = () => <Seo title="Contact" />

export default ContactPage

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

